/* checkbox */
.ProfileInfo_switch__bBXjq {
  position: relative;
  display: inline-block;
  width: 42px;
  height: 22px;
}
.ProfileInfo_slider__O--1U {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
  border-radius: 4px;
}
.ProfileInfo_slider__O--1U:before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 5px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
}
.ProfileInfo_switchInput__2Ldua:checked + .ProfileInfo_slider__O--1U {
  background-color: #5cdb95;
}
.ProfileInfo_switchInput__2Ldua:focus + .ProfileInfo_slider__O--1U {
  box-shadow: 0 0 1px #5cdb95;
}
.ProfileInfo_switchInput__2Ldua:checked + .ProfileInfo_slider__O--1U:before {
  -webkit-transform: translateX(18px);
  transform: translateX(18px);
}
